body {
  font-family: "Open Sans", "Arial, sans-serif";
  margin: 0;
  line-height: 24px;
  min-width: 320px;
}

html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}


// TABLE min-width: 660px//////////////////////////////////////////////////////////

@media (min-width: 660px) {
  body {
    min-width: 660px;
  }


}// min-width: 660px



// DESCTOP min-width: 1200px///////////////////////////////////////////////////////

@media (min-width: 1200px) {
  body {
    min-width: 1200px;
  }


}// min-width: 1200px
