.description {
  height: 1000px;
  position: relative;
  margin: 0 auto;
  margin-bottom: 2px;
  overflow: hidden;
  position: relative;
  &::before {
    position: absolute;
    content: "";
    top: 255px;
    left: 0;
    z-index: -1;
    background-color: #f2f2f2;
    width: 100%;
    height: 700px;
  }
  &::after {
    position: absolute;
    content: "";
    top: 176px;
    left: 0;
    z-index: -1;
    background-image: url("../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    width: 100%;
    height: 94px;
    transform: rotate(180deg);
  }
}


.description__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  color: $midnight;
  text-align: center;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 30px;
  position: relative;
  &::before {
    position: absolute;
    content: "";
    top: 681px;
    left: 82px;
    z-index: 1;
    background: url("..//img/iphone-screen-mobile@1x.png") no-repeat;
    background-size: 157px 320px;
    width: 157px;
    height: 320px;
    @media (min-resolution: 1.5dppx) {
      background-image: url("..//img/iphone-screen-mobile@2x.png");
    }
  }
  &::after {
    position: absolute;
    content: "";
    top: 956px;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 49px;
    margin: 0 auto;
    background-color: #fff;
  }
}


 .description__sub-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  color: $midnight;
  text-align: center;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 122px;
}

.description__list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  text-align: center;

}


.description__item-title {
  font-size: 18px;
  color: $midnight;
  line-height: 10px;
}

.description__text {
  font-size: 14px;
  color: $midnight;
  width: 264px;
  margin: 0 auto;
}


.description__item {
  width: 320px;
  height: 120px;
  margin: 0 auto;
}


// TABLE min-width: 660px//////////////////////////////////////////////////////

@media (min-width: 660px) {
  .description {
    height: 725px;
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: 240px;
      left: 0;
      z-index: -1;
      background-color: #f2f2f2;
      width: 100%;
      height: 425px;
    }
  }

  .description__list {
    margin-left: 318px;
    text-align: left;
  }

  .description__text {
    margin: 0;
    width: 310px;
  }

  .description__item {
    margin-bottom: 24px;
  }

  .description__title {
    font-size: 40px;
    width: 620px;
    margin-bottom: 9px;
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: 183px;
      left: 0;
      z-index: 1;
      background: url("..//img/iphone-screen-3-tablet@1x.png") no-repeat;
      width: 268px;
      height: 481px;
      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-3-tablet@2x.png");
        background-size: 268px 481px;
      }
    }
    &::after {
      display: none;
    }
  }

  .description__sub-title {
    font-size: 40px;
    line-height: 49px;
    width: 340px;
  }

  .description__table {
    display: flex;
  }

  .description__item-title {
    padding-top: 3px;
  }


} //*min-width: 660px





// DESCTOP min-width: 1200px//////////////////////////////////////////////////////

@media (min-width: 1200px) {
  .description {
