      left: 0;
      z-index: -1;
      background-color: #f2f2f2;
      width: 100%;
      height: 500px;
    }
    &::after {
      position: absolute;
      content: "";
      top: 241px;
      transform: rotate(180deg);
  }
}

  .description__title-block {
