  .description__sub-title {
    font-size: 50px;
    width: 430px;
    height: 140px;
    margin: 0 auto;
    line-height: 59px;
  }

  // .description__item {
  //   width: 33%;
  // }

  .description__item-title {
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    width: 230px;
    margin-bottom: 25px;
  }

  .description__item-phone-1 {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -30px;
      z-index: 1;
      background: url("..//img/iphone-screen-1-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;
      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-1-desktop@2x.png");
        background-size: 292px 302px;
    }
  }
}

  .description__item-phone-2 {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -27px;
      z-index: 1;
      background: url("..//img/iphone-screen-2-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;
      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-2-desktop@2x.png");
        background-size: 292px 302px;
    }
  }
}

  .description__item-phone-3 {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -27px;
      z-index: 1;
      background: url("..//img/iphone-screen-3-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;
      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-3-desktop@2x.png");
        background-size: 292px 302px;
    }
  }
}

  .description__list {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-left: 44px;
  }

  .description__text {
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    color: $midnight;
    width: 240px;
  }

} //*min-width: 1200px
