.contacts {
  text-align: center;
}

.contacts__block {
  position: absolute;
  z-index: 2;
  width: 100%;
}


.contacts__text-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;
  color: $midnight;
  width: 230px;
  margin: 0 auto;
  margin-bottom: 37px;
}



.contacts__list {
  list-style: none;
  padding: 0;
  margin: 0;

}


.contacts__title {
  font-size: 18px;
  font-weight: 700;
  color: $midnight;
}

.contacts__link {
  text-decoration: none;
}

.contacts__text {
  font-size: 24px;
  line-height: 28px;
  color: $cerise;
  width: 225px;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
}

.contacts__map {
  width: 100%;
  height: 645px;
}

.contacts__map-block {
  position: relative;
  &::before {
    position: absolute;
    content: "";
    top: -44px;
    left: 0;
    z-index: 1;
    background-color: #fff;
    width: 100%;
    height: 414px;
    // clip-path: polygon(0% 0%, 100% 0, 100% 90%, 49% 100%, 0 90%);
  }
  &::after {
    position: absolute;
    content: "";
    top: 336px;
    left: 0;
    z-index: 2;
    background-image: url("..//img/bg-triangle-white-footer.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% auto;
    width: 100%;
    height: 100px;
  }
}



// TABLE min-width:660px/////////////////////////////////////////////////////////


@media(min-width: 660px) {

  .contacts {
    height: 725px;
  }

  .contacts__text-title {
    font-size: 40px;
    line-height: 48px;
    width: 400px;
    margin: 0 auto;
    margin-bottom: 44px;
    padding-left: 15px;
  }

  .contacts__title {
    margin-left: 13px;
  }

  .contacts__list {
    display: flex;
    flex-wrap: wrap;
  }

  .contacts__item {
    width: 215px;
    height: 104px;
    margin: 0 auto;
  }
  .contacts__item:first-child{
  width: 50%;
  }

  .contacts__item:nth-child(2){
  width: 50%;
  }


  .contacts__map {
    width: 100%;
    height: 848px;
  }

  .contacts__map-block {
    position: absolute;
    width: 100%;
    &::before{
    content: "";
    top: -44px;
    left: 0;
    z-index: 1;
    background-color: #fff;
    width: 100%;
    height: 405px;
    // clip-path: polygon(0% 0%, 100% 0, 100% 86%, 50% 100%, 0 85%);
    }
    &::after {
      position: absolute;
      content: "";
      top: 320px;
      left: 0;
      z-index: 2;
      background-image: url("..//img/bg-triangle-white-footer.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: 100% auto;
      width: 100%;
      height: 150px;
    }
  }


  .contacts__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }



} //*min-width: 660px


// DESCTOP min-width: 1200px//////////////////////////////////////////////////


@media (min-width: 1200px) {

  .contacts__text-title {
    font-size: 50px;
    line-height: 57px;
    width: 500px;
    margin: 0 auto;
    margin-bottom: 60px;
  }

  .contacts__text {
    margin-top: 27px;
  }



 .contacts__map {
  width: 100%;
  height: 976px;
}

.contacts__list {
  display: flex;
  justify-content: space-around;
}

.contacts__item {
  width: 250px;
  margin: 0;
}

.contacts__item:first-child{
  width: 250px;
  }

  .contacts__item:nth-child(2){
  width: 250px;
  order: 2;
  }

  .contacts__map-block {
    position: absolute;
    &::before{
    content: "";
    top: -44px;
    left: 0;
    z-index: 1;
    background-color: #fff;
    width: 100%;
    height: 365px;
    // clip-path: polygon(0% 0%, 100% 0, 100% 78%, 50% 100%, 0 78%);
    }
    &::after {
      position: absolute;
      content: "";
      top: 285px;
      left: 0;
      z-index: 2;
      background-image: url(..//img/bg-triangle-white-footer.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 100% auto;
      width: 100%;
      height: 200px;
    }
  }

}//min-width: 1200px

